html {
    background-color: #e0dbf0;
}

h1 {
    text-align: left;
    padding-left: 2em;
}

.trips-container {
  background-color: #fff;
  margin-left: 5%;
  margin-right: 15%;
  width: 70%;
  display: flex;
  padding: 4em;
  border-radius: 70px;
}

.trips-div {
  display: flex;
  flex-direction: column;
  text-align: left;
  width: 200px;
  padding: 20px;
  margin: 5px;
  font-size: 14px;
  border: 1px solid lightgray;
  border-radius: 15px;
  p {
      padding: 0;
      margin: 0;
  }
}

form {

    display: flex;
    flex-direction: column;
    background-color: white;
    width: 300px;
    padding: 1.2em;
    background-color: #e0dbf0;
}
